@import '../../styles/abstracts/variables';
@import '../../styles/abstracts/functions';
@import '../../styles/themes/definitions-light';
@import '../../styles/abstracts/mixins';

:global {
    @import '../../styles/themes/default-theme';
    @import '../../styles/base/typography';
    @import '../../styles/base/css-reset';
    @import '../../styles/base/motion';
}
.badge {
    --bg: var(--grey-color-20);
    --label: var(--grey-color-80);
    background-color: var(--bg);
    color: var(--label);
    border-radius: $space-m;
    width: fit-content;
    padding: 0 $space-xxs;

    &.active {
        --bg: var(--primary-color-20);
        --label: var(--primary-color);

        &.disruptive {
            --bg: var(--disruptive-color-20);
            --label: var(--disruptive-color);
        }
    }
}
